import React from "react";

import GoogleLogin, { GoogleLoginResponse, GoogleLoginResponseOffline, GoogleLogout } from "react-google-login";
import styled from "styled-components";

/// <reference path="../node_modules/@types/gapi/index.d.ts" />
declare var gapi: any;

const googleClientId = "213841300532-aqinavnoi53if8cbk6re0aa6gkf965q3.apps.googleusercontent.com";

const App: React.FunctionComponent = () => {
    // const [auth, setAuth] = React.useState<GoogleLoginResponse>();
    // const onSuccess = (response: GoogleLoginResponse | GoogleLoginResponseOffline) => {
    //     console.log({ response });
    //     setAuth(response as GoogleLoginResponse);
    // };

    // const onFailure = (response: any) => {
    //     console.log({ response });
    // };

    // const onLogoutSuccess = () => {
    //     console.log("Logged out.");
    //     setAuth(undefined);
    // };

    ///
    const [signedIn, setSignedIn] = React.useState(false);
    const [content, setContent] = React.useState("");

    // Client ID and API key from the Developer Console
    var CLIENT_ID = "213841300532-aqinavnoi53if8cbk6re0aa6gkf965q3.apps.googleusercontent.com";
    var API_KEY = "AIzaSyBcke75SLGGCMiId3EcThDcLnbmnKasWyE";

    // Array of API discovery doc URLs for APIs used by the quickstart
    var DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/drive/v3/rest"];

    // Authorization scopes required by the API; multiple scopes can be
    // included, separated by spaces.
    var SCOPES = "https://www.googleapis.com/auth/drive.appdata";

    /**
     *  On load, called to load the auth2 library and API client library.
     */
    function handleClientLoad() {
        gapi.load("client:auth2", initClient);
    }

    /**
     *  Initializes the API client library and sets up sign-in state
     *  listeners.
     */
    function initClient() {
        gapi.client
            .init({
                apiKey: API_KEY,
                clientId: CLIENT_ID,
                discoveryDocs: DISCOVERY_DOCS,
                scope: SCOPES,
            })
            .then(
                function() {
                    // Listen for sign-in state changes.
                    gapi.auth2.getAuthInstance().isSignedIn.listen(updateSigninStatus);

                    // Handle the initial sign-in state.
                    updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
                },
                function(error: any) {
                    appendPre(JSON.stringify(error, null, 2));
                }
            );
    }

    /**
     *  Called when the signed in status changes, to update the UI
     *  appropriately. After a sign-in, the API is called.
     */
    function updateSigninStatus(isSignedIn: boolean) {
        setSignedIn(isSignedIn);
        isSignedIn && listFiles();
    }

    /**
     *  Sign in the user upon button click.
     */
    function handleAuthClick() {
        gapi.auth2.getAuthInstance().signIn();
    }

    /**
     *  Sign out the user upon button click.
     */
    function handleSignoutClick() {
        gapi.auth2.getAuthInstance().signOut();
    }

    /**
     * Append a pre element to the body containing the given message
     * as its text node. Used to display the results of the API call.
     *
     * @param {string} message Text to be placed in pre element.
     */
    function appendPre(message: string) {
        setContent(content + message + "\n");
    }

    /**
     * Print files.
     */
    function listFiles() {
        gapi.client.drive.files
            .list({
                pageSize: 10,
                fields: "nextPageToken, files(id, name)",
            })
            .then(function(response: any) {
                appendPre("Files:");
                var files = response.result.files;
                if (files && files.length > 0) {
                    for (var i = 0; i < files.length; i++) {
                        var file = files[i];
                        appendPre(file.name + " (" + file.id + ")");
                    }
                } else {
                    appendPre("No files found.");
                }
            });
    }
    ///

    return (
        <div>
            {/* {!auth ? (
                <GoogleLogin clientId={googleClientId} onSuccess={onSuccess} onFailure={onFailure} />
            ) : (
                <div>
                    <GoogleLogout clientId={googleClientId} onLogoutSuccess={onLogoutSuccess} />
                    <img src={auth.getBasicProfile().getImageUrl()} />
                    <div>{auth.getBasicProfile().getName()}</div>
                </div>
            )} */}
            {!signedIn ? <button onClick={handleAuthClick}>Authorize</button> : <button onClick={handleSignoutClick}>Sign Out</button>}

            <Content>{content}</Content>
            <script async defer src="https://apis.google.com/js/api.js" onLoad={handleClientLoad} />
        </div>
    );
};

export default App;

const Content = styled.pre`
    white-space: pre-wrap;
`